  flex-wrap: wrap;
}

.imageFrame {
  width: 220px;
  margin-right: 30px;
  margin-bottom: 30px;
  border-radius: 4px;
  overflow: hidden;
}

.main-content {
  width: calc(100% - 250px);
  display: flex;
  flex-direction: column;
  align-items: left;
}
